@font-face {
    font-family: 'Pretendard-Regular';
    src: url('https://fastly.jsdelivr.net/gh/Project-Noonnu/noonfonts_2107@1.1/Pretendard-Regular.woff') format('woff');
    font-weight: 400;
    font-style: normal;
}

.emplist {
    font-family: 'Pretendard-Regular';
    font-size: 20px;
    margin-top: 30px;
    margin-left: 25px;
    text-decoration: none;
}

.emplist a {
    color: #000000; /* 글자 색 지정 */
    text-decoration: none; /* 밑줄 제거 */
}

.emplist a:hover {
    color: #004ea2; /* 마우스 오버 시 글자 색 변경 */
}

.emplist li {
    margin-bottom: 20px; /* 리스트 항목 간 간격 조절 */
}

.homebg {
    width: 1000px;
    height: 600px;
}